const AgreementContainer = styled.div`
  width: 1540px;
  margin: 0 auto;

  h2 {
    margin-top: 240px;
    color: #333333;
    font-size: 60px;
    font-weight: 700;
    letter-spacing: -3.6px;
    line-height: 60px;
  }

  .document-tab {
    display: flex;
    margin-top: 100px;

    li {
      color: rgb(170, 170, 170);

      &:nth-child(2) {
        display: flex;
        align-items: center;
      }

      &:nth-child(2)::after,
      &:nth-child(2)::before {
        display: block;
        content: "";
        width: 1px;
        height: 16px;
        margin: 0 15px;
        background-color: rgb(229, 229, 229);
      }

      a {
        color: rgb(102, 102, 102);
        font-size: 20px;
        letter-spacing: -0.36px;
        line-height: 30px;
        text-decoration: none;

        &.active {
          color: #2f7d4e;
          font-weight: 700;
        }
      }
    }
  }

  .document-list {
    margin-bottom: 180px;

    .document {
      padding-top: 80px;

      h3 {
        color: #333;
        font-size: 20px;
        font-weight: 700;
        line-height: 1.5;
      }

      h4 {
        margin: 40px 0 20px 0;
        color: #333;
        font-size: 18px;
        font-weight: 700;
        line-height: 1.5;

        &:nth-child(2) {
          margin-top: 32px;
        }
      }

      p {
        color: #666;
        font-size: 18px;
        letter-spacing: -0.02em;
        line-height: 1.5;
      }

      ol {
        margin-top: 20px;
        list-style: decimal;

        &.hangul {
          list-style-type: hangul;
        }

        > li {
          margin-top: 13px;
          margin-left: 17px;
          color: #666;
          font-size: 18px;
          letter-spacing: -0.02em;
          line-height: 1.5;

          &:first-child {
            margin-top: 0;
          }

          > ol {
            margin-top: 10px;

            > li {
              margin-top: 10px;
              margin-left: 27px;
            }
          }
        }
      }
    }
  }
`;

