import React, { memo, useRef } from "react";
import styled from "styled-components";
import btn from "../../assets/images/srch-icon.png";
import DatePicker from "react-datepicker";

import { ko } from "date-fns/esm/locale";
import "react-datepicker/dist/react-datepicker.css";

import selectIcon from "../../assets/images/select-icon2.png";
import calendarIcon from "../../assets/images/calendar-icon.png";

const SearchWrap = styled.div`
  position: sticky;
  position: -webkit-sticky; /*    */
  top: 50px;
  .selectSearchWrap {
    display: flex;
    .search-border {
      width: 275px;
      margin: 0 0 0 20px;
    }
    select {
      background: url(${selectIcon}) right center no-repeat;
      border: none;
      border-bottom: 2px solid #000;
      justify-content: space-between;
      font-size: 19px;
      /* gap: 10px; */
    }
  }
  .dateWrap {
    border-bottom: 2px solid #000;
    display: flex;
    align-items: center;
    width: 350px;
    justify-content: space-between;
    input {
      /* border:1px solid red; */
      width: calc(100% - 10px);
      padding: 10px 0;

      display: inline-block;
      border: none !important;
      background: url(${calendarIcon}) right center no-repeat;
      color: #777;
      font-size: 18px;
      &:focus {
        outline: none;
      }
      &.dateLastPicker {
        margin: 0 0 0 10px;
        border: 1px solid red;
      }
    }
  }
  h1 {
    margin: 20px 0 40px 0;
    font-size: 60px;
    letter-spacing: -4px;
    font-weight: bold;
  }
  .search-border {
    border-bottom: 2px solid #000;
    width: 350px;
    display: flex;
    justify-content: space-between;
    align-items: top;

    input[type="search"] {
      border: none;
      font-size: 17px;

      width: 100%;
      /* margin: 20px 0 25px 0; */
      padding: 20px 0 25px 0;
      &:focus {
        outline: none;
      }
      &::placeholder {
        color: #aaa;
        font-size: 17px;
      }
    }
    button {
      background: url(${btn}) no-repeat;
      border: none;
      width: 35px;
      margin: 20px 0 0 0;
      display: inline-block;
      height: 35px;
      /* border: 1px solid red; */
      text-indent: -999px;
    }
  }
  .FAQ-notice {
    line-height: 1.5;
    margin: 70px 0 0 0;
    color: #333;
  }
  .titleAreaList {
    margin: 70px 0 0 0;
    li {
      line-height: 2;
      color: #888;

      font-weight: bold;
      &:before {
        content: "";
        padding: 0 5px 0 0;
        font-weight: bold;
      }

      span {
        color: #777;
        font-weight: normal;
      }
    }
  }
`;

const TitleArea = memo(({ title }) => {
  const [startDate, setStartDate] = React.useState("");
  const [endDate, setEndDate] = React.useState("");

  return (
    <SearchWrap>
      <h1>{title}</h1>

      {title === "" ? (
        <>
          <p className="FAQ-notice">
                
            <br />
              .
          </p>
        </>
      ) : title === " " ? (
        <>
          <div className="dateWrap">
            <DatePicker
              selected={startDate}
              onChange={(date) => setStartDate(date)}
              locale={ko} //  
              dateFormat="yyyy.MM.dd" //   
              // showPopperArrow={false} //  
            />{" "}
            ~
            <DatePicker
              className="dateLastPicker"
              selected={endDate}
              onChange={(date) => setEndDate(date)}
              locale={ko} //  
              dateFormat="yyyy.MM.dd" //   
              showPopperArrow={false} //  
              maxDate={new Date()} //     
            />
          </div>
          <div className="selectSearchWrap">
            <select>
              <option></option>
            </select>
            <div className="search-border">
              <input type="search" placeholder=" ." />
              <button></button>
            </div>
          </div>
        </>
      ) : (
        <>
          {" "}
          <div className="search-border">
            <input type="search" placeholder=" " />
            <button></button>
          </div>
        </>
      )}
      {title === " " ? (
        <ul className="titleAreaList">
          <li>
             <span>10:00~21:00</span>
          </li>
          <li>
             <span>123-1234-1234</span>
          </li>
          <li>
             <span>   </span>
          </li>
        </ul>
      ) : (
        <></>
      )}
      {title === " " ? (
        <>
          <p className="FAQ-notice">
                  
            <br />
                  .
          </p>
        </>
      ) : (
        <></>
      )}
    </SearchWrap>
  );
});

export default TitleArea;
