/**
 * @filename: Header.js
 * @description:  
 */
import React, { memo, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";

import styled from "styled-components";

import logo from "./assets/images/footer-logo.png";

const FooterArea = styled.footer`
  position: relative;
  width: calc(100% - 240px);
  padding: 100px 120px;
  border-top: 1px solid #e5e5e5;
  background: #fff;
  font-size: 16px;
  font-weight: 400;
  color: #666;
  &:after {
    content: "";
    display: block;
    clear: both;
  }
  a.logo {
    float: left;
    margin-right: 400px;
  }
  .footerdiv {
    float: left;

    .links {
      margin-bottom: 50px;
      a {
        display: inline-block;
        color: #666;
        line-height: 1.5;
        &::after {
          content: "";
          display: inline-block;
          width: 1px;
          height: 14px;
          margin: 0 7px 0 10px;
          background: #e5e5e5;
        }
        &:last-child:after {
          display: none;
        }
      }
    }
    .address {
      line-height: 2;
      strong {
        &::after {
          content: "";
          display: inline-block;
          width: 1px;
          height: 14px;
          margin: 0 7px 0 10px;
          background: #e5e5e5;
        }
        &:last-child:after {
          display: none;
        }
      }
    }
    .copy {
      margin-top: 40px;
      font-weight: normal;
      color: #aaa;
      line-height: 1;
    }
  }
`;

const Footer = memo(({ setOpenEmail }) => {
  //      
  const location = useLocation();
  let pathname = location.pathname;

  // useEffect(() => {
  //   console.log(location);
  // }, [location]);

  return (
    <FooterArea>
      <Link to="/" className="logo">
        <img src={logo} alt="  " />
      </Link>
      <div className="footerdiv">
        <div className="links">
          <Link to="/Introduce">   </Link>
          <Link to="/Agreement/Home"></Link>
          <Link to="/Sitemap"></Link>
          <Link
            to={`${pathname}`}
            onClick={() => {
              setOpenEmail(true);
            }}
          >
            
          </Link>
        </div>
        <div className="address">
          <p>
            <strong>    42</strong>
            <strong> :  2021--****</strong>
          </p>
          <p>
            <strong> : </strong>
            <strong> : 1661-1234</strong>
            <strong> : 123-45-67890</strong>
          </p>
        </div>
        <p className="copy">
          COPYRIGHT 2021 LOTTEWORLD ADVENTURE BUSAN. ALL RIGHT RESERVED.
        </p>
      </div>
    </FooterArea>
  );
});

export default Footer;
