import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
import { cloneDeep } from "lodash";

// const URL = "http://localhost:3001";
//   //

//   
export const getPayment = createAsyncThunk(
  "PaymentSlice/getPayment",
  async (payload, { rejectWithValue }) => {
    let result = null;
    try {
      result = await axios.get(
        `https://sheeps1026.github.io/backend/members.json`,
        {
          params: {
            id: payload?.id,
          },
        }
      );
    } catch (e) {
      result = rejectWithValue(e.reponse);
    }
    return result;
  }
);
// 
export const getPaymentInfo = createAsyncThunk(
  "PaymentSlice/getPaymentInfo",
  async (payload, { rejectWithValue }) => {
    let result = null;
    try {
      result = await axios.get(
        `https://sheeps1026.github.io/backend/res_info.json`,
        {
          params: {
            merchant_uid: payload?.merchant_uid,
            paymentDay: payload?.paymentDay,
            paymentMonth: payload?.paymentMonth,
          },
        }
      );
    } catch (e) {
      result = rejectWithValue(e.reponse);
    }
    return result;
  }
);
//  &   
export const postPaymentInfo = createAsyncThunk(
  "PaymentSlice/postPaymentInfo",
  async (payload, { rejectWithValue }) => {
    let result = null;
    try {
      result = await axios.post(
        `https://sheeps1026.github.io/backend/res_info.json`,
        {
          pg: "html5_inicis", // PG
          pay_method: payload?.pay_method, // 
          merchant_uid: payload?.merchant_uid, // 
          amount: payload?.amount, // 
          name: payload?.name, // 
          paymentDate: payload?.paymentDate, //  
          buyer_name: payload?.buyer_name, //  
          buyer_tel: payload?.buyer_tel, //  
          buyer_email: payload?.buyer_email, //  
          visit_name: payload?.visit_name, //  
          visit_tel: payload?.visit_tel, //  
          visit_mail: payload?.visit_mail, //  
          buyer_addr: payload?.buyer_addr, //  
          buyer_postcode: payload?.buyer_postcode, //  
          priceA: payload?.priceA, // 
          priceY: payload?.priceY, // 
          priceC: payload?.priceC, // 
          numberA: payload?.numberA, // 
          numberY: payload?.numberY, // 
          numberC: payload?.numberC, // 
          paymentDay: payload?.paymentDay, //
          paymentMonth: payload?.paymentMonth, //
          paymentHour: payload?.paymentHour, //
        }
      );
      // console.log(payload.name+"  ???????");
    } catch (e) {
      result = rejectWithValue(e.reponse);
      console.log("", e);
    }
    return result;
  }
);

//   
export const deletePaymentInfo = createAsyncThunk(
  "PaymentSlice/deletePaymentInfo",
  async (payload, { rejectWithValue }) => {
    let result = null;
    try {
      result = await axios.delete(
        `https://sheeps1026.github.io/backend/res_info.json/${payload?.id}`
      );
    } catch (err) {
      result = rejectWithValue(err.message);
    }
    return result;
  }
);
const PaymentSlice = createSlice({
  name: "PaymentSlice",
  initialState: {
    data: null,
    loading: false,
    error: null,
  },
  reducers: {},
  extraReducers: {
    [getPayment.pending]: (state, { payload }) => {
      return { ...state, loading: true };
    },
    [getPayment.fulfilled]: (state, { payload }) => {
      return {
        data: payload?.data,
        loading: false,
        error: null,
      };
    },
    [getPayment.rejected]: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: {
          code: payload?.data?.rt
            ? payload?.data?.rt
            : payload?.status
            ? payload.status
            : 500,
          message: payload?.data?.rtmsg
            ? payload?.data?.rtmsg
            : payload?.statusText
            ? payload.statusText
            : "Server Error",
        },
      };
    },
    [getPaymentInfo.pending]: (state, { payload }) => {
      return { ...state, loading: true };
    },
    [getPaymentInfo.fulfilled]: (state, { payload }) => {
      return {
        data: payload?.data,
        loading: false,
        error: null,
      };
    },
    [getPaymentInfo.rejected]: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: {
          code: payload?.data?.rt
            ? payload?.data?.rt
            : payload?.status
            ? payload.status
            : 500,
          message: payload?.data?.rtmsg
            ? payload?.data?.rtmsg
            : payload?.statusText
            ? payload.statusText
            : "Server Error",
        },
      };
    },
    [postPaymentInfo.pending]: (state, { payload }) => {
      return { ...state, loading: true };
    },
    [postPaymentInfo.fulfilled]: (state, { payload }) => {
      const data = cloneDeep(state.data);

      console.log(data);

      return {
        data: data,
        loading: false,
        error: null,
      };
    },
    [postPaymentInfo.rejected]: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: {
          code: payload?.data?.rt
            ? payload?.data?.rt
            : payload?.status
            ? payload.status
            : 500,
          message: payload?.data?.rtmsg
            ? payload?.data?.rtmsg
            : payload?.statusText
            ? payload.statusText
            : "Server Error",
        },
      };
    },
    [deletePaymentInfo.pending]: (state, { payload }) => {
      return { ...state, loading: true };
    },
    [deletePaymentInfo.fulfilled]: (state, { meta, payload }) => {
      const data = cloneDeep(state.data);

      const index = data.findIndex(
        (element) => element.merchant_uid === meta.arg.merchant_uid
      );
      if (index !== undefined) {
        data.splice(index, 1);
      }
      console.log(state.data);
      return {
        data: data,
        loading: false,
        error: null,
      };
    },
    [deletePaymentInfo.rejected]: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: {
          code: payload?.data?.rt
            ? payload?.data?.rt
            : payload?.status
            ? payload.status
            : 500,
          message: payload?.data?.rtmsg
            ? payload?.data?.rtmsg
            : payload?.statusText
            ? payload.statusText
            : "Server Error",
        },
      };
    },
  },
});

export default PaymentSlice.reducer;
