import React, { memo } from "react";
import styled from "styled-components";
import checkboxOff from "../../assets/images/checkbox-icon.png";
import checkboxOn from "../../assets/images/checkbox-icon-on.png";
import selectBg from "../../assets/images/select-icon2.png";
import useAxios from "axios-hooks";
import regexHelper from "../../libs/RegexHelper";
import { createRoutesFromChildren, useNavigate } from "react-router-dom";
const InquiryWriteWrap = styled.div`
  width: 50%;
  margin: 240px auto;
  position: relative;
  ul {
    padding: 30px 40px;
    background: #f2f2f2f2;
    width: calc(100% - 80px);
    li {
      color: #888;
      line-height: 1.5;
      font-size: 17px;
      padding: 5px 0;
      font-weight: 400;
      &:before {
        position: absolute;
        left: 30px;
        content: "";
        font-size: 15px;
        font-weight: bold;
      }
    }
  }
  h4 {
    font-weight: bold;
    font-size: 26px;
    margin: 20px 0;
    letter-spacing: -1px;
    margin: 60px 0 20px;
    span {
      color: green;
    }
  }
  h5 {
    font-weight: bold;
    font-size: 20px;
    margin: 20px 0;
    &:after {
      content: "";
      font-weight: bold;
      color: green;
    }
  }
  .ScroolDiv {
    max-height: 150px;
    overflow-y: scroll;
    overflow-x: hidden;
    border: 1px solid #ccc;
    padding: 40px 30px;
    width: 100%;
    box-sizing: border-box;
    &::-webkit-scrollbar {
      width: 15px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #ccc;
      border-radius: 20px;
      border: 3px solid #fff;
    }
    &::-webkit-scrollbar-track {
      background-color: #fff;
    }
    h3 {
      font-weight: bold;
      color: #777;
      font-size: 19px;
      line-height: 2;
    }
    p {
      font-size: 18px;
      line-height: 1.5;
      color: #777;
    }
  }
  .CheckBoxWrap {
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
    input[type="checkbox"] {
      display: none;
      & + label {
        padding: 5px 35px;
        background: url(${checkboxOff}) left center no-repeat;
        line-height: 1.5;
        font-size: 19px;
        color: #444;
      }
      &:checked + label {
        background: url(${checkboxOn}) left center no-repeat;
      }
    }
  }
  .titleWrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid #000;
    .essentialTxt {
      color: green;
      &:before {
        content: "";
        font-weight: bold;
      }
    }
  }
  .selectSt {
    -o-appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding: 15px 15px;
    width: 250px;
    color: #000 !important;
    font-size: 19px;
    border: 1px solid #eee;
    background: url(${selectBg}) 90% 50% no-repeat;
    &:placeholder {
      color: #777;
    }
  }
  .inputSt {
    padding: 15px 15px;
    width: 250px;
    color: #777;
    font-size: 19px;
    border: 1px solid #eee;
  }
  .txtareaSt {
    width: 100%;
    color: #777;
    font-size: 19px;
    border: 1px solid #eee;
    resize: none;
    height: 230px;
    padding: 10px;
  }
  .fileCon {
    display: flex;
    align-items: center;
    .fileAddBtn {
      display: none;
      & + label {
        display: inline-block;
        font-size: 18px;
        border: 1px solid #000;
        padding: 15px 20px;
        font-weight: bold;
        border: 1px solid #222;
      }
    }
    .fileWrap {
      display: flex;
      align-items: center;
      span {
        display: inline-block;
      }
      .fileName {
        line-height: 1.5;
        width: 150px;
        text-overflow: ellipsis;
        overflow: hidden;
        margin: 0 0 0 10px;
      }
    }
  }
  .noticeTxt {
    line-height: 1.5;
    margin: 20px 0px;
    color: #222;
    font-size: 18px;
  }
  .resetBtn {
    width: 160px;
    background: none;
    font-size: 20px;
    font-weight: bold;
    color: #777;
    border: 1px solid #777;
    padding: 15px 30px;
    border-radius: 50px;
    bottom: 0px;
    position: relative;
    overflow: hidden;
    z-index: 1;
    /* &:hover {
      color: #fff; */
    &:after {
      left: 0;
      content: "";
      display: block;
      width: 160px;
      border-radius: 50%;
      height: 160px;
      background: #777;
      position: absolute;
      /* bottom: 20px; */
      top: 60px;
      transition: all 0.4s;
      z-index: -1;
    }
    &:hover {
      color: #fff;
      border: 1px solid #777;
      &:after {
        top: -50px;
      }
    }
  }
  .submitBtn {
    width: 160px;
    background: none;
    font-size: 20px;
    font-weight: bold;
    color: #000;
    border: 1px solid #000;
    padding: 15px 30px;
    border-radius: 50px;
    bottom: 0px;
    position: relative;
    overflow: hidden;
    z-index: 1;
    margin: 0 0 0 20px;
    /* &:hover {
      color: #fff; */
    &:after {
      left: 0;
      content: "";
      display: block;
      width: 160px;
      border-radius: 50%;
      height: 160px;
      background: green;
      position: absolute;
      /* bottom: 20px; */
      top: 60px;
      transition: all 0.4s;
      z-index: -1;
    }
    &:hover {
      color: #fff;
      border: 1px solid green;
      &:after {
        top: -50px;
      }
    }
  }
`;

const InquiryWrite = () => {
  const navigate = useNavigate();
  //     Ajax   
  const [{ loading }, refetch] = useAxios(
    {
      url: "https://sheeps1026.github.io/backend/bbs_inquire.json",
      method: "POST",
    },
    { menual: true }
  );
  const onSubmit = React.useCallback((e) => {
    e.preventDefault();
    const current = e.target;

    try {
      //
      regexHelper.value(current.division, " ");
      //
      regexHelper.value(current.name, " ");
      regexHelper.minLength(
        current.name,
        2,
        "  2 ."
      );
      regexHelper.maxLength(
        current.name,
        10,
        "  10 ."
      );
      //
      regexHelper.value(current.mail, "   .");
      regexHelper.emailCheck(current.mail, "   .");
      //
      regexHelper.value(current.title, " ");
      regexHelper.minLength(
        current.title,
        "  2  ."
      );
      regexHelper.maxLength(
        current.title,
        "  100  ."
      );
      //
      regexHelper.value(current.content, " ");
      regexHelper.minLength(
        current.content,
        "  2  ."
      );
      regexHelper.maxLength(
        current.content,
        "  100  ."
      );
    } catch (e) {
      window.alert(e.message);
      console.log(e);
      return;
    }
    let json = null;

    (async () => {
      try {
        const response = await refetch({
          data: {
            I_name: current.name.value,
            I_division: current.division.value,
            I_mail: current.mail.value,
            I_title: current.title.value,
            I_content: current.content.value,
            I_file: current.file.value,
          },
        });
        json = response.data;
        console.log("");
      } catch (e) {
        console.error(e);
        console.log("");
        // window.alert(`[${e.response.status}] ${e.response.statusText}\n${e.message}`);
      }

      // ?
      if (json !== null) {
        window.alert("  .");
        navigate("/customer/InquiryWrite");
      }
    })();
  });

  return (
    <InquiryWriteWrap>
      <ul>
        <li>   .</li>
        <li>
            1      ., 
                  .
        </li>
        <li>
           (,       )
                .
        </li>
      </ul>
      <h4>
        , <span>()</span>
      </h4>
      <div className="ScroolDiv">
        <h3>   ,  </h3>
        <p>
                   
           ,  .
        </p>
        <h3>   ,  </h3>
        <p>
          1.          
           ,  . 2.   
            ,   ,  
            3.  ,      
                , 
          .
        </p>
      </div>
      <form onSubmit={onSubmit}>
        <div className="CheckBoxWrap">
          <input
            type="checkbox"
            id="checkBoxWrap"
            name="I_check"
            className="field"
          />
          <label htmlFor="checkBoxWrap">.</label>
        </div>
        <div className="titleWrap">
          <h4></h4>
          <span className="essentialTxt">  .</span>
        </div>
        <h5></h5>
        <select className="selectSt" name="division">
          <option value=""></option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
        </select>
        <h5></h5>
        <input
          type="text"
          className="inputSt"
          placeholder=" ."
          name="name"
        />
        <h5></h5>
        <input
          type="mail"
          className="inputSt"
          placeholder=" ."
          name="mail"
          // ref={(el) => (inputRef.current[3] = el)}
        />
        <h5></h5>
        <input
          type="text"
          className="inputSt"
          placeholder=" ."
          name="title"
          // ref={(el) => (inputRef.current[4] = el)}
        />
        <h5></h5>
        <textarea
          type="text"
          className="txtareaSt"
          placeholder=" ."
          name="content"
          // ref={(el) => (inputRef.current[5] = el)}
        />
        <h5></h5>
        <div className="fileCon">
          <input
            type="file"
            className="fileAddBtn field"
            id="fileAdd"
            name="file"
            // ref={(el) => (inputRef.current[6] = el)}
          />
          <label htmlFor="fileAdd"></label>
          <div className="fileWrap">
            <span className="fileName">
              filenamefilenamefilenamefilenamefilenamefilename
            </span>
            <span>.png</span>
          </div>
        </div>
        <p className="noticeTxt">
            (jpg,png,jpeg,pdf,pptx,xisx,docx)  1  .
        </p>
        <h5></h5>
        <p className="noticeTxt">
                  
            .
        </p>
        <button type="reset" className="resetBtn">
          
        </button>
        <button type="submit" className="submitBtn">
          
        </button>
      </form>
    </InquiryWriteWrap>
  );
};

export default InquiryWrite;
