const AgreementContainer = styled.div`
  width: 1540px;
  margin: 0 auto;

  h2 {
    margin-top: 240px;
    font-size: 60px;
    font-weight: 700;
    letter-spacing: -3.6px;
    line-height: 60px;
  }

  .document-tab {
    display: flex;
    margin-top: 100px;

    li {
      color: rgb(170, 170, 170);

      &:nth-child(2) {
        display: flex;
        align-items: center;
      }

      &:nth-child(2)::after,
      &:nth-child(2)::before {
        display: block;
        content: "";
        width: 1px;
        height: 16px;
        margin: 0 15px;
        background-color: rgb(229, 229, 229);
      }

      a {
        color: rgb(102, 102, 102);
        font-size: 20px;
        letter-spacing: -0.36px;
        line-height: 30px;
        text-decoration: none;

        &.active {
          color: rgb(47, 125, 78);
          font-weight: 700;
        }
      }
    }
  }

  .document-list {
    margin-top: 80px;
    margin-bottom: 180px;

    .document {
      margin: 40px 0 20px 0;

      h3 {
        margin-bottom: 20px;
        color: #333;
        font-size: 18px;
        font-weight: 700;
        line-height: 1.5;
      }

      p {
        color: #666;
        font-size: 18px;
        letter-spacing: -0.02em;
        line-height: 1.5;
      }

      table {
        width: 100%;
        margin-top: 20px;

        thead {
          background-color: #f8f8f8;

          tr {
            th {
              padding: 20px 20px 15px 20px;
              border: 1px solid #e5e5e5;
              border-top: 1px solid #000;
              color: rgb(51, 51, 51);
              font-size: 18px;
              font-weight: 700;
              letter-spacing: -0.36px;
              line-height: 27px;
            }
          }
        }

        tbody {
          tr {
            td {
              text-align: center;
              vertical-align: middle;
              padding: 20px 20px 15px 20px;
              border: 1px solid #e5e5e5;
              color: rgb(102, 102, 102);
              font-size: 18px;
              letter-spacing: -0.36px;
              line-height: 27px;
            }
          }
        }
      }

      ol {
        margin-top: 20px;
        list-style: decimal;

        &.hangul {
          list-style-type: hangul;
        }

        > li {
          margin-top: 13px;
          margin-left: 17px;
          color: #666;
          font-size: 18px;
          letter-spacing: -0.02em;
          line-height: 1.5;

          &:first-child {
            margin-top: 0;
          }

          > ol {
            margin-top: 10px;

            > li {
              margin-top: 10px;
              margin-left: 27px;
            }
          }
        }
      }
    }
  }
`;

