import React, { memo } from "react";
import styled from "styled-components";
import bg from "../../assets/images/bg_pc_visual.png";
import resultimg from "../../assets/images/paymentresult.jpg";
import { NavLink, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getPaymentInfo } from "../../slice/PaymentSlice";

const MypageContainer = styled.div`
  width: 100vw;
  height: auto;
  background: url(${bg});
  padding: 20px 0 50px;

  .pageContainer {
    width: 900px;
    background: #fff;
    margin: 0 auto 0;
    padding: 0 0 40px 0;

    h3 {
      text-align: center;
      font-weight: 500;
      font-size: 28px;
      box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
      z-index: 1;
      padding: 20px 0;
    }
    .result-p {
      text-align: center;
      font-weight: bold;
      font-size: 20px;
    }
    img.resultImg {
      display: block;
      margin: 16px auto;
    }
    h4 {
      font-size: 20px;
      margin: 30px 50px 10px;
      font-weight: 500;
    }
    .paymentList {
      display: block;
      cursor: pointer;
      background: #2b72c9;
      margin: 20px auto;
      color: #fff;
      line-height: 1.5;
      padding: 13px 0;
      font-size: 17px;
      width: 400px;
      border: none;
      border-radius: 3px;
      text-align: center;
      transition: all 0.2s;
      &:hover{
        background: #000;
        /* color:#2b72c9 !important; */
        padding: 13px 0;
        
      }
    }
    .PaymentListTable {
      border-top: 2px solid #000;
      margin: 0 auto 30px;
      width: 800px;
      border-bottom: 2px solid #ccc;
      tr {
        th,
        td {
          vertical-align: middle;
          padding: 15px 10px;
          font-size: 15px;
          border-top: 1px solid #ccc;
        }
        th {
          background: #f1f1f1;
          border-right: 1px solid #ccc;
        }
        td {
          &.colorRed {
            color: red;
          }
          p {
            line-height: 1.5;
          }
        }
      }
    }
    .paymentFooter {
      background: #f1f1f1;
      padding: 0 0 40px 0;
      .paymentFooterTitle {
        width: 800px;
        margin: 0 auto;
        padding: 20px 0 0 0;
        line-height: 1.5;
        font-size: 18px;
      }
      ul {
        width: 800px;
        margin: 5px auto;
        li {
          font-size: 16px;
          color: #222;
          line-height: 2;
          &:before {
            content: "";
            padding: 0 5px 0 0;
          }
          ul {
            li {
              color: #666;
              &:before {
                color: #f1f1f1;
              }
            }
          }
        }
      }
    }
  }
`;
const PaymentResult = memo(() => {
  const { state } = useLocation();
  console.log(state);

  const dispatch = useDispatch();
  const { data, loading, error } = useSelector((state) => state.PaymentSlice);

  React.useEffect(() => {
    dispatch(getPaymentInfo({ merchant_uid: state }));
    
  }, [dispatch]);

  // const datas = data[0];
  
  // console.log(data);


  return (
    <>
      {data ? (
        <MypageContainer>
          <div className="pageContainer">
            <h3> </h3>
            <img className="resultImg" src={resultimg} alt="" />
            <p className="result-p"> .</p>
            <NavLink
              to="/TicketingPage/PaymentView"
              state={{data:data[0].merchant_uid}}
              className="paymentList"
              type="button"
              
            >
               
            </NavLink>
            <table className="PaymentListTable">
              <tbody>
                <tr>
                  <th></th>
                  <td>{data[0].merchant_uid}</td>
                </tr>
                <tr>
                  <th></th>
                  <td>{data[0].name}</td>
                </tr>
                <tr>
                  <th></th>
                  {/* <td> 2 ()</td> */}
                  <td>
                    
                      {
                        data[0].numberA ? <p> {data[0].numberA } ()
                        </p>:(
                          <></>
                        )
                      }                      
                      {
                        data[0].numberY ? <p> {data[0].numberY } ()
                        </p>:(
                          <></>
                        )
                      }       
                      {
                        data[0].numberC ? <p> {data[0].numberC } ()
                        </p>:(
                          <></>
                        )
                      }       
                      
                    
                  </td>
                </tr>
                {/* <tr>
                  <th></th>
                  <td>
                    <p>1234-1234-1234-1234</p>
                    <p>1234-1234-1234-1234</p>
                  </td>
                </tr> */}
                <tr>
                  <th></th>
                  <td>{data[0].paymentDate}</td>
                  
                </tr>
                <tr>
                  <th> </th>
                  <td className="colorRed">{data[0].amount}</td>

                </tr>
                <tr>
                  <th></th>
                  <td>{data[0].pay_method}</td>
                </tr>
                {/* <tr>
                  <th></th>
                  <td className="colorRed">26,900</td>
                </tr> */}
              </tbody>
            </table>
            <div className="paymentFooter">
              <p className="paymentFooterTitle">  </p>
              <ul>
                <li>
                        .{" "}
                </li>

                <li>
                    [ &#62;] 
                  .
                </li>
                <li>   .</li>
                <li>
                      . ,  
                     .
                </li>
              </ul>
              <p className="paymentFooterTitle"> </p>
              <ul>
                <li>     .</li>
                <li>
                  
                  <ul>
                    <li> 1.      URL </li>
                    <li> 2.      </li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
        </MypageContainer>
      ) : (
        <>
           
        </>
      )}
    </>
  );
});

export default PaymentResult;
