import React, { memo } from "react";
import styled from "styled-components";

import CloseBtn from "../../assets/images/components/Email/close.png";

const Background = styled.div`
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
`;

const PaymentChkContainer = styled.div`
  width: 504px;
  height: 516px;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 15px;

  header {
    position: relative;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    padding: 20px 0;

    h1 {
      padding: 0 30px;
      color: #222;
      font-size: 22px;
      font-weight: 700;
    }

    button {
      position: absolute;
      top: 20px;
      right: 10px;
      border: none;
      background: none;
      cursor: pointer;

      img {
        width: 20px;
        height: 20px;
        opacity: 0.5;
      }
    }
  }

  .pop-mid {
    height: 320px;
    padding: 15px 40px;
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    overflow-y: scroll;

    ul {
      line-height: 1.5;

      li {
        margin-top: 20px;
        font-size: 15px;
      }
    }
  }

  .pop-bottom {
    display: flex;
    justify-content: space-between;
    margin: 18px 25px;

    button {
      width: 222px;
      border: 1px solid #2b72c9;
      border-radius: 3px;
      background-color: #fff;
      color: #2b72c9;
      font-size: 16px;
      line-height: 60px;
      cursor: pointer;

      &:nth-child(2) {
        background-color: #2b72c9;
        color: #fff;
      }
    }
  }
`;

const PaymentChk3 = memo(({ setPaymentChk3 }) => {
  return (
    <Background>
      <PaymentChkContainer>
        <header>
          <h1>       </h1>
          <button onClick={() => setPaymentChk3(false)}>
            <img src={CloseBtn} alt="" />
          </button>
        </header>
        <div className="pop-mid">
          <ul>
            <li>
              1.  :     ,  , 
               
            </li>
            <li>
              2. : () , , E-mail, , , ,
              , , E-mail, SMS <br />
              <span>() , </span>
            </li>
            <li>
              3.  :
              <br /> <span>()    </span>
              <br /> <span>()  2</span>
            </li>
            <p>
                  ,     ,   
                  .
            </p>
            <p>
                     .,
                      
              .
            </p>
          </ul>
        </div>
        <div className="pop-bottom">
          <button onClick={() => setPaymentChk3(false)}></button>
          <button onClick={() => setPaymentChk3(false)}></button>
        </div>
      </PaymentChkContainer>
    </Background>
  );
});

export default PaymentChk3;
