import React, { memo } from "react";
import styled from "styled-components";

import CloseBtn from "../assets/images/components/Email/close.png";
import Img from "../assets/images/components/Email/email.png";

const Background = styled.div`
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
`;

const EmailContainer = styled.div`
  width: 900px;
  height: 675px;
  padding: 0 40px;
  background-color: #fff;

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    h2 {
      padding: 39px 0;
      color: rgb(51, 51, 51);
      font-size: 26px;
      font-weight: 700;
      letter-spacing: -1.56px;
      line-height: 39px;
    }

    button {
      border: none;
      background: none;
      font-size: 30px;
      cursor: pointer;
      z-index: 20;

      img {
        width: 24px;
        height: 24px;
      }
    }
  }

  div.inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 40px;

    img {
      width: 200px;
      height: 200px;
    }

    h3 {
      margin-top: 60px;
      color: rgb(51, 51, 51);
      font-size: 32px;
      font-weight: 600;
      letter-spacing: -0.36px;
      line-height: 48px;
    }

    p {
      text-align: center;
      margin-top: 25px;
      color: #666;
      font-size: 18px;
      letter-spacing: -0.36px;
      line-height: 30px;

      strong {
        color: #000;
        font-weight: 700;
      }

      &:nth-child(4) {
        margin-top: 60px;
        color: rgb(136, 136, 136);
        font-size: 18px;
        letter-spacing: -0.36px;
        line-height: 27px;
      }
    }
  }
`;

const Email = memo(({ setOpenEmail }) => {
  return (
    <Background>
      <EmailContainer>
        <header>
          <h2></h2>
          <button onClick={() => setOpenEmail(false)}>
            <img src={CloseBtn} alt="" />
          </button>
        </header>
        <div className="inner">
          <img src={Img} alt="" />
          <h3>  .</h3>
          <p>
                   ,
               
            <br />       
                ,
            <br />
            <strong>      </strong>
             .
          </p>
          <p>2007.07.25</p>
        </div>
      </EmailContainer>
    </Background>
  );
});

export default Email;
