import React, { memo, useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";

const SignupContainer = styled.form`
  width: 460px;
  margin: 0 auto;
  padding-bottom: 100px;

  h2 {
    text-align: center;
    padding: 60px 0 20px 0;
    font-size: 44px;
    font-weight: 700;
  }

  .info {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;

    label {
      margin: 19px 0 8px 0;
      font-size: 14px;
      font-weight: 700;
    }

    input {
      padding: 10px 110px 10px 14px;
      border: 1px solid rgb(218, 218, 218);
    }

    .info-rrn {
      margin-top: 19px;
      margin-bottom: 8px;

      div {
        display: flex;
        align-items: center;
        margin-top: 8px;

        input {
          width: 100%;
        }

        p {
          margin: 0 20px;
        }
      }
    }
  }

  button {
    width: 100%;
    padding: 15px 0;
    border: none;
    border-radius: 3px;
    background-color: #000;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
  }
`;

const Signup = memo(() => {
  const history = useNavigate();

  const [inpval, setInpval] = useState({
    id: "",
    pwd: "",
    name: "",
    tel: "",
    email: "",
  });

  const [data, setData] = useState([]);
  console.log(inpval);

  const getData = (e) => {
    // console.log(e.target.value);

    // value, name 
    const { value, name } = e.target;

    //  
    // console.log(value, name);

    setInpval(() => {
      return {
        ...inpval,
        [name]: value,
      };
    });
  };

  const addData = (e) => {
    e.preventDefault();

    const { id, pwd, name, tel, email } = inpval;

    if (id === "") {
      alert(" ");
    } else if (pwd === "") {
      alert(" ");
    } else if (name === "") {
      alert(" ");
    } else if (tel === "") {
      alert(" ");
    } else if (email === "") {
      alert(" ");
    } else if (!email.includes("@")) {
      alert("   .");
    } else {
      alert(" ");

      localStorage.setItem("members", JSON.stringify([...data, inpval]));

      history("/TicketingPage/Login");
    }
  };

  return (
    <SignupContainer>
      <h2></h2>
      <div className="info">
        <label htmlFor=""></label>
        <input type="text" name="id" onChange={getData} placeholder="" />

        <label htmlFor=""></label>
        <input
          type="text"
          name="pwd"
          onChange={getData}
          placeholder=""
        />

        <label htmlFor=""></label>
        <input type="text" name="name" onChange={getData} placeholder="" />

        <label htmlFor=""></label>
        <input
          type="text"
          name="tel"
          onChange={getData}
          placeholder=""
        />

        <label htmlFor=""></label>
        <input
          type="text"
          name="email"
          onChange={getData}
          placeholder=""
        />
      </div>
      <button type="submit" onClick={addData}>
        
      </button>
      {/* </Link> */}
    </SignupContainer>
  );
});

export default Signup;
